import React from 'react';
import Helmet from 'react-helmet';
const SEO_DATA = {
  url: 'https://rearia.io',
  author: 'Ansin Lau',
  keywords: ['hearing aids', 'earphones', 'rearia'],
  twitter: {
    id: '@ansinlau',
  },
  facebook: {
    id: '',
  },
};

const SEO = ({title, desc, img}) => {
  return (
    <Helmet>
      <meta property="fb:app_id" content={SEO_DATA.facebook.id} />
      <meta property="og:title" content={title} />
      <meta property="og:type" content="website" />
      <meta property="og:url" content={SEO_DATA.url} />
      <meta property="og:image" content={img} />
      <meta property="og:description" content={desc} />

      <meta name="twitter:card" content="summary_large_image" />
      <meta name="twitter:creator" content="@hackinoutco" />
      <meta name="twitter:site" content="@hackinoutco" />
      <meta name="twitter:title" content={title} />
      <meta name="twitter:description" content={desc} />
      <meta name="twitter:domain" content={SEO_DATA.url} />
      <meta name="twitter:image:src" content={img} />

      <meta name="description" content={desc} />
      <meta name="keywords" content={SEO_DATA.keywords.join(', ')} />
      <meta name="author" content={SEO_DATA.author} />
      <title>{title}</title>
      <html lang="en" />
    </Helmet>
  );
};

export default SEO;
